"use client";

import React from "react";
import { Menu } from "antd";
import {
  DashboardOutlined,
  UsergroupAddOutlined,
  ShoppingOutlined,
  FileSearchOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import Link from "next/link";
import { usePathname } from "next/navigation";

export default function AdminSidebar() {
  const pathname = usePathname() || "/admin";
  // compute selected key, fallback to dashboard
  const parts = pathname.split("/").filter(Boolean); // ['admin', 'users', ...]
  const selected = parts[1] || "dashboard";

  // clean items array so Menu renders correctly and shows nice spacing
  const items = [
    {
      key: "dashboard",
      icon: <DashboardOutlined />,
      label: <Link href="/admin">Dashboard</Link>,
    },
    {
      key: "users",
      icon: <UsergroupAddOutlined />,
      label: <Link href="/admin/users">Users</Link>,
    },
    {
      key: "orders",
      icon: <ShoppingOutlined />,
      label: <Link href="/admin/orders">Orders</Link>,
    },
    {
      key: "reports",
      icon: <FileSearchOutlined />,
      label: <Link href="/admin/reports">Reports</Link>,
    },
    {
      key: "settings",
      icon: <SettingOutlined />,
      label: <Link href="/admin/settings">Settings</Link>,
    },
  ];

  return (
    <div className="h-full flex flex-col">
      {/* brand area */}
      <div className="px-6 py-6">
        <div className="text-lg font-bold">SainVandeBharat</div>
        <div className="text-xs text-zinc-500 mt-1">Admin Portal</div>
      </div>

      {/* menu - make it scrollable if long */}
      <div className="flex-1 overflow-auto px-2 pb-6">
        <Menu
          mode="inline"
          selectedKeys={[selected]}
          items={items}
          style={{
            borderRight: "none",
            background: "transparent",
          }}
        />
      </div>

      {/* optional footer area inside sidebar (small) */}
      <div className="px-4 py-4 border-t">
        <div className="text-xs text-zinc-500">v1.0 â€¢ Admin</div>
      </div>
    </div>
  );
}
